body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-content: stretch;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: rgb(5, 5, 5);

  &__result {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    text-align: right;
    font-size: 44px;
    width: 330px;
    height: 118px;
    padding: 0px 25px 0px 0px;
    overflow: hidden;

    .previus-expression {
      height: 44px;
      width: auto;
      transition: 0.7s all ease;
      font-size: 24px;
      color: rgb(143, 136, 136);
    }

    .result-in-procces {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      height: 44px;
      width: auto;
      transition: 0.7s all ease;
      font-size: 24px;
      color: rgb(143, 136, 136);
      margin: 15px 0;
      span {
        line-height: 24px;
      }
    }
    .current-expression {
      transition: 0.7s all ease;
      margin: 0 0 15px 0;
      height: 44px;
      width: auto;
    }

    .very-long-expression {
      transition: 0.7s all ease;
      font-size: 34px;
      margin: 0 0 15px 0;
      height: 44px;
      width: auto;
    }

    .very-very-long-expression {
      transition: 0.7s all ease;
      font-size: 24px;
      margin: 0 0 15px 0;
      height: 44px;
      width: auto;
    }
  }

  &__table {
    text-align: center;
    line-height: 28px;
    border-spacing: 10px;
    font-size: 28px;

    td {
      cursor: pointer;
      padding: 15px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: rgb(92, 91, 91);
    }

    .simple-signs {
      background-color: rgb(228, 136, 15);
    }

    .special-signs {
      background-color: rgb(182, 182, 182);
      color: black;
    }

    .activeKey {
      background-color: rgb(170, 170, 170);
    }
  }
}

#zero {
  border-radius: 35px;
  text-align: left;
  padding: 0px 0px 0px 30px;
}
